<template>
  <div class="question-page">
    <div class="status">
      <status-bar
        :total="questionLength"
        :current="parseInt($route.params.id, 10)"
        :goTo="goTo"
      ></status-bar>
    </div>
    <transition-manager>
      <div
        class="question"
        v-for="(question, id) in questions"
        v-if="$route.params.id - 1 === id"
      >
        <div class="question-title">
          <h2><pre>{{question.title}}</pre></h2>
        </div>
        <div class="question-img" v-if="question.img">
          <img :src="question.img" />
        </div>
        <div class="choices row center-xs">
          <div
            :class="choiceClassname(question.choices.length) + (question.choiceBtn ? ' choice-btn' : '')"
            v-for="choice in question.noShuffle ? question.choices : shuffle(question.choices)"
            @click="choice.onClick"
          >
            <div v-if="choice.img" class="choice-image">
              <img :src="choice.img" />
            </div>
            <div class="choice-text">
              <pre>{{choice.text}}</pre>
            </div>
          </div>
        </div>
        <div class="skip" v-if="question.skippable">
          <btn :route="skip(id)">切歌</btn>
        </div>
      </div>
    </transition-manager>
  </div>
</template>

<script>
import { shuffle } from 'lodash';
import StatusBar from '../components/StatusBar';
import TransitionManager from '../components/TransitionManager';
import Btn from '../components/Btn';
import questions from '../data/questions';

export default {
  components: {
    StatusBar,
    TransitionManager,
    Btn,
  },
  data() {
    return {
      questionLength: questions.length,
    };
  },
  computed: {
    questions() {
      const {
        $router,
        $store,
        questionLength,
      } = this;
      return questions.map((question, index) => ({
        ...question,
        title: question.title,
        choices: question.choices.map(choice => ({
          ...choice,
          onClick() {
            const qIndex = index + 1;
            switch (question.type) {
              case 'quiz': {
                $store.commit('answer', {
                  type: question.type,
                  key: question.key || qIndex,
                  value: choice.correct,
                });
                break;
              }
              case 'score': {
                $store.commit('answer', {
                  type: question.type,
                  key: question.key || qIndex,
                  value: choice.score,
                });
                if (question.valued) $store.commit('noteValued', { key: question.key || qIndex });
                break;
              }
              case 'meta': {
                $store.commit('answer', {
                  type: question.type,
                  key: question.key || qIndex,
                  value: choice.value || choice.text,
                });
                break;
              }
              default:
            }

            $router.push(qIndex === questionLength ?
              '/result' : `/question/${qIndex + 1}`);
          },
        })),
      }));
    },
  },
  methods: {
    shuffle,
    goTo(target) {
      const index = parseInt(this.$route.params.id, 10);
      if (target < index) this.$router.push(`/question/${target}`);
    },
    skip(id) {
      return id + 1 === this.questionLength ? '/result' : `/question/${id + 2}`;
    },
    choiceClassname(count) {
      switch (count) {
        case 1:
          return 'choice col-xs-7';
        case 2:
        case 4:
          return 'choice col-xs-6';
        case 3:
        case 6:
          return 'choice col-xs-4';
        default:
          return 'choice';
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import '../scss/settings';
.question-page {
  width: 100%;
  position: relative;
}

.question-title {
  pre {
    font-family: $fonts;
    margin: 0;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}

.question {
  width: 100%;
  position: absolute;
  top: 4em;
}

.question-img {
  width: 50%;
  margin: 0 auto 2em;
}

.choice {
  width: 100%;
  padding: 0 3%;
  display: inline-block;
  cursor: pointer;
  transform: scale(1);
  transition: transform 0.2s ease;
  box-sizing: border-box;
}

.choice:hover {
  transform: scale(1.1);
  transition: transform 0.2s ease;
}


.choice-text {
  text-align: center;

  pre {
    font-family: $fonts;
    font-size: 1em;
    color: $content-color;
    font-weight: 400;
    white-space: pre-wrap;
    word-wrap: break-word;
    margin: 0.5em 0;
    // text-align: justify;
  }
}

.choice-btn {
  .choice-text {
    border: 2px solid $dark-gray;
    margin-bottom: 1em;
    padding: 0 0.5em;
    pre {
      margin: 0.5em 0;
    }
  }
}

.choice-image {
  margin: 0.5em 0;
}

.skip {
  margin-top: 1em;
}
</style>



